@font-face {
  font-family: "Uertas Regular";
  src: url("/src/resources/fonts/UertasRegular.ttf");
}
@font-face {
  font-family: "BDO Grotesk";
  src: url("/src/resources/fonts/BDOGrotesk-VF.ttf");
}
@font-face {
  font-family: "Coupeur Jack Semibold";
  src: url("/src/resources/fonts/CoupeurJack-Semibold.otf");
}
.markdown {
  margin: 5rem;
  color: #1b1b1b;
  background-color: #fcf5da;
  padding: 30px 5%;
}
.markdown .title {
  font-size: 5rem;
  border-bottom: #fcf5da solid 2px;
  border-color: #1b1b1b;
  text-align: center;
  display: block;
  margin-bottom: 4rem;
}
.markdown h1 {
  font-family: "BDO Grotesk";
  font-size: min(3.5rem, 52px);
  font-weight: 900;
  margin-top: 1rem;
  margin-bottom: 3rem;
  border-bottom: #fcf5da solid 2px;
  border-color: #1b1b1b;
}
.markdown h3 {
  font-family: "BDO Grotesk";
  font-size: 4.3rem;
  font-weight: 100;
  text-transform: uppercase;
  font-size: 1.9rem;
  text-transform: none;
  font-weight: 400;
  margin-top: 0.5rem;
  margin-bottom: 0.5rem;
}
.markdown p, .markdown li {
  font-family: "BDO Grotesk";
  font-size: min(1.5rem, 24px);
}
.markdown p {
  line-height: 2.5rem;
}
.markdown blockquote {
  text-align: center;
  margin: 2rem 0;
}
.markdown blockquote p {
  font-size: 2.2rem;
}

