@use "./typeface.scss";
@use "./colors.scss";
// ============= VARIABLES ================

// FONTS
@mixin font-display {
  font-family: "Uertas";
  font-size: 10.3rem;
}

@mixin font-h2 {
  font-family: "BDO Grotesk";
  font-size: 4.3rem;
  font-weight: 100;
  text-transform: uppercase;
}

@mixin font-h3 {
  font-family: "BDO Grotesk";
  font-size: min(3.5rem, 52px);
  font-weight: 900;
}

@mixin font-h4 {
  font-family: "BDO Grotesk";
  font-size: 2.5rem;
  font-weight: 900;
  text-transform: uppercase;
}

@mixin font-decoration {
  font-family: "Coupeur Jack";
  font-size: 1.9rem;
}

@mixin font-body {
  font-family: "Consolas";
  font-size: min(1.5rem, 24px);
}

@mixin font-body-alternative {
  font-family: "BDO Grotesk";
  font-size:  min(1.5rem, 24px);
}

// SIZES
$sizes: (
  "margin-1": 30px,
  "margin-2": 100px,
  "border": colors.$light
    solid 2px,
);
