@use "global" as *;
@use "colors";

$border-color: (
  light: colors.$light,
  dark: colors.$dark,
);

@mixin border($color, $side: "") {
  #{"border" + $side}: map-get($map: $border-color, $key: $color) solid 2px;
}

@mixin flex($dir: row) {
  display: flex;
  flex-direction: $dir;
}

$section-color: (
  light: colors.$dark,
  dark: colors.$light,
);

$section-background: (
  light: colors.$light,
  dark: colors.$transparent,
);

@mixin section($colors: dark) {
  color: map-get($map: $section-color, $key: $colors);
  background-color: map-get($map: $section-background, $key: $colors);
}

@mixin text {
  padding: map-get($map: $sizes, $key: "margin-1") 5%;
}
