@use './modules/global.scss' as *;
@use './modules/colors.scss';
@use './modules/util.scss' as *;

.markdown {
  margin: 5rem;
  @include section(light);
  @include text;

  .title {
    font-size: 5rem;
    border-bottom: map-get($map: $sizes, $key: "border");
    border-color: colors.$dark;
    text-align: center;
    display: block;
    margin-bottom: 4rem;
  }

  h1 {
    @include font-h3();
    margin-top: 1rem;
    margin-bottom: 3rem;
    border-bottom: map-get($map: $sizes, $key: "border");
    border-color: colors.$dark;
  }

  h3 {
    @include font-h2();
    font-size: 1.9rem;
    text-transform: none;
    font-weight: 400;
    margin-top: 0.5rem;
    margin-bottom: 0.5rem;
  }

  p, li {
    @include font-body-alternative();
  }

  p {
    line-height: 2.5rem;
  }

  blockquote {
    text-align: center;
    margin: 2rem 0;
    
    p {
      font-size: 2.2rem;
    }
  }

}