@font-face {
  font-family: "Uertas Regular";
  src: url("/src/resources/fonts/UertasRegular.ttf");
}
@font-face {
  font-family: "BDO Grotesk";
  src: url("/src/resources/fonts/BDOGrotesk-VF.ttf");
}
@font-face {
  font-family: "Coupeur Jack Semibold";
  src: url("/src/resources/fonts/CoupeurJack-Semibold.otf");
}
.main {
  position: relative;
  background: rgb(0, 0, 0);
  background: radial-gradient(circle, rgba(0, 0, 0, 0.6) 70%, rgba(0, 0, 0, 0.8) 100%);
  background-attachment: fixed;
  padding: 30px;
}
.main .image {
  background-image: url("/src/resources/images/background.jpg");
  background-attachment: fixed;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: -1;
}
.main .content {
  padding: 30px;
  padding-right: 60px;
}
.main .decorations .d2, .main .decorations .d3 {
  display: flex;
  flex-direction: row;
  position: absolute;
  right: 30px;
  align-items: center;
  flex-direction: row-reverse;
  gap: 30px;
}
.main .decorations .d2 .line1, .main .decorations .d3 .line1 {
  width: 200px;
  height: 2px;
  background-color: #fcf5da;
}
.main .decorations .d2 .line2, .main .decorations .d3 .line2 {
  flex-grow: 1;
  height: 2px;
  background-color: #fcf5da;
}
.main .decorations .d2 {
  top: 30px;
  width: calc(100vw - 460px);
}
.main .decorations .d3 {
  bottom: 30px;
  width: calc(100vw - 120px);
}
.main .decorations .d4 {
  display: flex;
  flex-direction: row;
  position: absolute;
  bottom: 60px;
  left: 60px;
}
.main .decorations .d4 p {
  font-size: 18px;
}

.header {
  color: #1b1b1b;
  background-color: #fcf5da;
  display: flex;
  flex-direction: row;
  align-items: center;
  position: fixed;
  left: 20px;
  top: 20px;
  border: 2px solid black;
  z-index: 2;
}
.header a img {
  height: 32px;
  width: 32px;
  margin: 10px 15px;
}

