@use "./modules/global.scss" as *;
@use "modules/util" as *;
@use "modules/colors.scss";

.main {

  position: relative;

  //Background
  background: rgb(0, 0, 0);
  background: radial-gradient(
    circle,
    rgba(0, 0, 0, 0.6) 70%,
    rgba(0, 0, 0, 0.8) 100%
  );
  background-attachment: fixed;

  //Background Image
  .image {
    background-image: url("/src/resources/images/background.jpg");
    background-attachment: fixed;
    background-position: center;
    background-size:cover;
    background-repeat: no-repeat;
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    z-index: -1;

  }

  padding: map-get($map: $sizes, $key: "margin-1");

  .content {
    padding: map-get($map: $sizes, $key: "margin-1");
    padding-right: calc(map-get($map: $sizes, $key: "margin-1") * 2);
  }
  
  .decorations {
    .d2, .d3 {
      @include flex(row);
      position: absolute;
      
      right: map-get($map: $sizes, $key: "margin-1");
      align-items: center;
      flex-direction: row-reverse;
      gap: map-get($map: $sizes, $key: "margin-1");
  
      .line1 {
        width: 200px;
        height: 2px;
        background-color: colors.$light;
      }
  
      .line2 {
        flex-grow: 1;
        height: 2px;
        background-color: colors.$light;
      }
    }
    .d2 {
      top: map-get($map: $sizes, $key: "margin-1");
      width: calc(100vw - (map-get($map: $sizes, $key: "margin-1") * 2 + 400px));
    }
    .d3 {
      bottom: map-get($map: $sizes, $key: "margin-1");
      width: calc(100vw - map-get($map: $sizes, $key: "margin-1") * 4);
    }
    .d4 {
      @include flex(row);
      position: absolute;
      bottom: calc(map-get($map: $sizes, $key: margin-1) * 2);
      left: calc(map-get($map: $sizes, $key: margin-1) * 2);
      p {
        font-size: 18px;
      }
    }
  }
}

.header {
  @include section(light);
  @include flex(row);

  align-items: center;
  
  position: fixed;
  left: 20px;
  top: 20px;
  border: 2px solid black;
  z-index: 2;

  a {
    img {
      height: 32px;
      width: 32px;
      margin: 10px 15px;
    }
  }
}
