@use "modules/util.scss" as *;
@use "modules/colors.scss";
@use "modules/global" as *;

.intro-text {
  border-left: 2px solid black;
}
.projects {
  @include flex(row);

  min-height: 80vh;
  height: fit-content;

  .left {
    @include section(light);
    @include flex(column);
    @include text();

    flex-basis: 25%;

    .top-title {
      flex-grow: 1;

      h2 {
        @include border("dark", "-bottom");
      }
    }

    .links {
      flex-basis: 10%;
    }
  }

  .right {
    @include section(dark);
    flex-grow: 1;
    max-width: 66%;
  }
}

.contact {
  @include flex(row);

  height: 100vh;
  justify-content: center;
  align-items: center;
  position: relative;

  .text {
    @include section("dark");
    @include text();

    text-align: center;
    width: fit-content;
  }
}

.front {
  @include flex(row);

  position: relative;
  flex-grow: 2;
  flex-basis: auto;
  height: 100vh;
  align-items: center;

  .text {
    * {
      width: fit-content;
    }

    h5 {
      position: relative;
      top: -1rem;
      @include border(colors.$light, "-top");
    }
  }
}

.who-am-i {
  @include flex(row);
  height: fit-content;
  min-height: 80vh;

  .left {
    @include section(dark);
    @include text();
    @include flex(column);
    gap: 20px;

    .card {
      @include border("");
      @include flex(column);
      
      gap: 10px;
      padding: 20px;

      .icons {
        height: 40px;
        @include flex(row);
        gap: 8px;

        img {
          height: 100%
        }
      }
    }
  }
  .right {
    @include section(light);
    @include flex(column);
    @include text();
    flex-basis: 40%;

    h2 {
      @include border(dark, "-bottom");
    }
  }
}
