@font-face {
  font-family: "Uertas Regular";
  src: url("/src/resources/fonts/UertasRegular.ttf");
}
@font-face {
  font-family: "BDO Grotesk";
  src: url("/src/resources/fonts/BDOGrotesk-VF.ttf");
}
@font-face {
  font-family: "Coupeur Jack Semibold";
  src: url("/src/resources/fonts/CoupeurJack-Semibold.otf");
}
.intro-text {
  border-left: 2px solid black;
}

.projects {
  display: flex;
  flex-direction: row;
  min-height: 80vh;
  height: fit-content;
}
.projects .left {
  color: #1b1b1b;
  background-color: #fcf5da;
  display: flex;
  flex-direction: column;
  padding: 30px 5%;
  flex-basis: 25%;
}
.projects .left .top-title {
  flex-grow: 1;
}
.projects .left .top-title h2 {
  border-bottom: #1b1b1b solid 2px;
}
.projects .left .links {
  flex-basis: 10%;
}
.projects .right {
  color: #fcf5da;
  background-color: rgba(0, 0, 0, 0.5);
  flex-grow: 1;
  max-width: 66%;
}

.contact {
  display: flex;
  flex-direction: row;
  height: 100vh;
  justify-content: center;
  align-items: center;
  position: relative;
}
.contact .text {
  color: #fcf5da;
  background-color: rgba(0, 0, 0, 0.5);
  padding: 30px 5%;
  text-align: center;
  width: fit-content;
}

.front {
  display: flex;
  flex-direction: row;
  position: relative;
  flex-grow: 2;
  flex-basis: auto;
  height: 100vh;
  align-items: center;
}
.front .text * {
  width: fit-content;
}
.front .text h5 {
  position: relative;
  top: -1rem;
  border-top: solid 2px;
}

.who-am-i {
  display: flex;
  flex-direction: row;
  height: fit-content;
  min-height: 80vh;
}
.who-am-i .left {
  color: #fcf5da;
  background-color: rgba(0, 0, 0, 0.5);
  padding: 30px 5%;
  display: flex;
  flex-direction: column;
  gap: 20px;
}
.who-am-i .left .card {
  border: solid 2px;
  display: flex;
  flex-direction: column;
  gap: 10px;
  padding: 20px;
}
.who-am-i .left .card .icons {
  height: 40px;
  display: flex;
  flex-direction: row;
  gap: 8px;
}
.who-am-i .left .card .icons img {
  height: 100%;
}
.who-am-i .right {
  color: #1b1b1b;
  background-color: #fcf5da;
  display: flex;
  flex-direction: column;
  padding: 30px 5%;
  flex-basis: 40%;
}
.who-am-i .right h2 {
  border-bottom: #1b1b1b solid 2px;
}

